import React from "react"
import style from "../assets/scss/index.module.scss";
import classNames from "classnames";
import {library} from "@fortawesome/fontawesome-svg-core";
import {faBriefcase, fas} from "@fortawesome/free-solid-svg-icons";
import {fab} from "@fortawesome/free-brands-svg-icons";
import {FontAwesomeIcon} from "@fortawesome/react-fontawesome";
import {connponents} from "../component/resume";
import {MDXRenderer} from "gatsby-plugin-mdx";
import {MDXProvider} from "@mdx-js/react";
import SEO from "../component/seo";
import Img from "gatsby-image";
import {SkillDashboard} from "../component/skill-dashboard";
import {graphql} from "gatsby";

const ResumeExperiences: React.FunctionComponent<any> = (props: any) => {
    return <div className={classNames(style.card, style.border0, style.gMb40)}>
        <div className={classNames(style.uHeadingV3, style.gMb40)}>
            <h2 className={classNames(style.h2, style.uHeadingV3Title)}>
                <FontAwesomeIcon width={14} icon={faBriefcase}
                                 className={classNames(style.gPosRel, style.gTop1, style.gMr5)}/>
                工作經歷
            </h2>
        </div>

        <div
            className={classNames(style.uInfoV11, style.gBgWhiteGradientV1After, style.gPa0)}>
            <div className={classNames(style.cScrollbar, style.gPb40)}>
                <ul className={classNames(style.row, style.listUnstyled)}>
                    {
                        props.exps.map((item: any, index: number) => {
                            const {frontmatter: job, body} = item.node;
                            return <li className={classNames(style.colMd12,
                                style.gBrdBottom,
                                style.gBrd0Md,
                                style.gBrdGrayLightV4,
                                style.animateAnimated,
                                style.animateFadeInDown)} key={index}>
                                <div className={classNames(style.row)}>
                                    <div
                                        className={classNames(style.colMd4,
                                            style.gPy15,
                                            style.textMdRight,
                                            style.gPr40Md,
                                            style.gMb20,
                                            style.gMb0Md,
                                            style.gBrdGrayLightV4,
                                            style.gBrdRight)}>
                                        <h4 className={classNames(style.h4, style.gFontWeight300)}>{job.company}</h4>
                                        <h5 className={classNames(style.h5, style.gFontWeight300)}>{job.title}</h5>
                                        <h6 className={classNames(style.h6, style.gFontWeight300, style.mb0)}>{job.during}</h6>
                                    </div>
                                    <div className={classNames(style.colMd8,
                                        style.alignSelfCenter,
                                        style.gOrientationLeft,
                                        style.gPl40Md,
                                        style.gBrdDarkblueLeft,
                                        style.gBrd5)}>
                                        <div
                                            className={classNames(style.gHiddenSmDown, style.uTimelineV2Icon, style.gTop35)}>
                                            <i className={classNames(style.dBlock,
                                                style.gWidth18,
                                                style.gHeight18,
                                                style.gBgPurple,
                                                style.gBrdAround,
                                                style.gBrd4,
                                                style.gBrdGrayLightV4,
                                                style.roundedCircle)}/>
                                        </div>
                                        <article className={classNames(style.gPb50)}>
                                            <MDXProvider components={connponents}>
                                                <MDXRenderer>{body}</MDXRenderer>
                                            </MDXProvider>
                                        </article>
                                    </div>
                                </div>
                            </li>
                        })
                    }
                </ul>
            </div>

        </div>
    </div>
}

const ResumeProfile: React.FunctionComponent<any> = (props: any) => {
    const {
        links,
        job_title,
        birthday,
        email,
        education,
        about
    } = props;
    return <>
        <div className={classNames(style.dFlex, style.alignItemsCenter, style.justifyContentSmBetween, style.gMb5)}>
            <h2 className={classNames(style.gFontWeight300, style.gMr10)}>Bruno Jan</h2>
            <ul className={classNames(style.listInline, style.mb0)}>
                {
                    links.map((item: any) => {
                        return <li className={classNames(style.listInlineItem, style.gMx2)}
                                   key={item.title}>
                            <a className={classNames(style.uIconV1,
                                style.uIconSizeSm,
                                style.uIconSlideUpHover,
                                style.gColorGrayLightV1,
                                style.gBgGrayLightV5,
                                style.gColorGrayLightV1Hover,
                                style.roundedCircle)}
                               href={item.url}
                               target={"_blank"}>
                                <i className={classNames(style.gFontSizeDefault, style.gLineHeight1, style.uIconElemRegular)}>
                                    <FontAwesomeIcon width={8} height={14}
                                                     icon={item.icon}/>
                                </i>
                                <i className={classNames(style.gFontSizeDefault, style.gLineHeight1, style.uIconElemHover)}>
                                    <FontAwesomeIcon width={8} height={14}
                                                     icon={item.icon}/>
                                </i>
                            </a>
                        </li>
                    })
                }
            </ul>
        </div>
        <h6 className={classNames(style.h6, style.gFontWeight300, style.gMb10)}>
            <i className={classNames(style.gPosRel, style.gTop1, style.gMr5, style.gColorGrayDarkV5)}>
                <FontAwesomeIcon width={10.5} height={14}
                                 icon={{prefix: "fas", iconName: 'award'}}/>
            </i>
            {job_title}
        </h6>
        <ul className={classNames(style.listInline, style.gFontWeight300)}>
            <li className={classNames(style.listInlineItem, style.gMr20)}>
                <i className={classNames(style.gPosRel, style.gTop1, style.gMr8, style.gColorGrayDarkV5)}>
                    <FontAwesomeIcon width={10.5} height={14}
                                     icon={{prefix: "fas", iconName: 'birthday-cake'}}/>
                </i>
                {birthday}
            </li>
            <li className={classNames(style.listInlineItem, style.gMr20)}>
                <i className={classNames(style.gPosRel, style.gTop1, style.gMr8, style.gColorGrayDarkV5)}>
                    <FontAwesomeIcon width={10.5} height={14}
                                     icon={{prefix: "fas", iconName: 'at'}}/>
                </i>
                {email}
            </li>
            <li className={classNames(style.listInlineItem, style.gMr20)}>
                <i className={classNames(style.gPosRel, style.gTop1, style.gMr8, style.gColorGrayDarkV5)}>
                    <FontAwesomeIcon width={10.5} height={14}
                                     icon={{prefix: "fas", iconName: 'graduation-cap'}}/>
                </i>
                {education}
            </li>
        </ul>
        <hr className={classNames(style.gBrdGrayLightV4, style.gMy20)}/>
        <p className={classNames(style.lead, style.gLineHeight18)}>
            {about}
        </p>
    </>
}

const Resume: React.FunctionComponent<any> = (props: any) => {
    library.add(fas, fab);
    const {
        allConfigYaml: {edges: [{node: data}]},
        allMdx: {edges: exps}
    }: any = props.data

    return <>
        <SEO title={"Bruno Jan Resume"} description={props.description}/>
        <section className={classNames(style.gLayoutBoxed, style.gBgiV8)}>
            <main className={classNames(style.container)}>
                <div className={classNames(style.row, style.justifyContentBetween, style.gPt100)}>
                    <div className={classNames(style.col12)}>
                        <div className={classNames(style.gBrdAround, style.gBrdGrayLightV4, style.gPa20, style.gMb40)}>
                            <div className={style.row}>
                                <div className={classNames(style.col4)}>
                                    <div className={style.gPy30}>
                                        <Img
                                            className={classNames(style.imgFluid, style.w100)}
                                            fluid={props.data.imageSharp.fluid}
                                            fadeIn={true}
                                            alt="Bruno Jan"/>
                                    </div>
                                </div>
                                <div className={style.col8}>
                                    <ResumeProfile {...data} />
                                    <hr className={classNames(style.gBrdGrayLightV4, style.gMy20)}/>
                                    <SkillDashboard skills={data.skills}/>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div className={classNames(style.colLg12)}>
                        <ResumeExperiences exps={exps}/>
                    </div>
                </div>
            </main>
        </section>
    </>
}

export const pageQuery = graphql`
    query {
        imageSharp(fluid: {originalName: {eq: "myimage.jpg"}}) {
            fluid(maxWidth: 400) {
                ...GatsbyImageSharpFluid
            }
        }
        allConfigYaml {
            edges {
                node {
                    about
                    job_title
                    birthday
                    email
                    education
                    links {
                        title
                        url
                        icon {
                            iconName
                            prefix
                        }
                    }
                    skills {
                        title
                        value
                        color {
                            front
                            back
                            text
                        }
                    }
                }
            }
        }
        allMdx(filter: {frontmatter: {category: {eq: "experiences"}}}, sort: {fields: frontmatter___during, order: DESC}) {
            edges {
                node {
                    frontmatter {
                        company
                        title
                        during
                    }
                    body
                }
            }
        }
    }
`

export default Resume;